external fun initRadios()
external fun initCheckboxes()

abstract class InputToDial(debug: Boolean=false): Params(debug=debug) {

    init {
        // Set year to 10 years from now
        getInputField(YEAR)?.value = (DateTimeUTC.now().year + 10).toString()

        // Watch each input change to signal right or wrong
        watchInput(LATITUDE, ::latitude)
        watchInput(LONGITUDE, ::longitude)
        watchInput(AZIMUTH, ::azimuth)
        watchInput(ALTITUDE, ::altitude)
        watchInput(HOUR_SPAN, ::hourSpan)
        watchInput(TZ_OFFSET_HOUR, ::tzOffsetHour)
        watchInput(MIN_ALTITUDE_ABOVE, ::minAltitudeAboveDial)
        watchInput(YEAR, ::year)
        watchInput(ANNIVERSARY,::anniversary)

        // Button click listeners
        listenForClick(EAST) {
            setAzAlt("90","0")
        }
        listenForClick(SOUTH) {
            setAzAlt("180", "0")
        }
        listenForClick(WEST) {
            setAzAlt("270","0")
        }
        listenForClick(NORTH) {
            setAzAlt("0","0")
        }
        listenForClick(ZENITH) {
            setAzAlt("0","90")
        }
        // Buttons EQUATORIAL_NORTH/SOUTH depends on the latitude being set already
        listenForClick(EQUATORIAL_NORTH) {
            CoroutineScope(Dispatchers.Main).launch {
                val lat = latitude()
                delay(10)

                if (lat is Error)
                    printLn(GREEN,"Latitude must be set to compute equatorial north")
                else
                    setAzAlt("0", fix6(lat.value))
            }
        }
        listenForClick(EQUATORIAL_SOUTH) {
            CoroutineScope(Dispatchers.Main).launch {
                val lat = latitude()
                delay(10)

                if (lat is Error)
                    printLn(GREEN,"Latitude must be set to compute equatorial south")
                else
                    setAzAlt("180", fix6(-lat.value))
            }
        }
        watchInput(WIDTH, ::width)
        watchInput(HEIGHT, ::height)

        watchInput(EOT_N, ::eotN)

        listenForClick(A3FMT) {
            setWH(A3.width.toString(), A3.height.toString())
        }
        listenForClick(A4FMT) {
            setWH(A4.width.toString(), A4.height.toString())
        }

        // Try to load cookie for any new value in the name field
        (document.getElementById("name") as HTMLInputElement?)?.let { input ->
            input.addEventListener("input",{ _: Event ->
                val name = input.value
                loadCookie(name)
            })
        }

        document.getElementById("save")?.addEventListener("click",{ _: Event ->
            saveAsCookie()

            (document.getElementById("name") as HTMLInputElement?)?.let { input ->
                val name = input.value.trim()
                if (name.isNotEmpty())
                    // Add name to the datalist in the name field
                    document.getElementById("parameters")?.let { datalist ->
                        datalist.innerHTML += "<option value=\"$name\">${name.uppercase()}</option>"
                    }
            }
        })

        (document.getElementById(TZ_OFFSET) as HTMLSpanElement?)?.let { span ->
            // When the TZ_OFFSET radio button state is changed ..
            span.addEventListener("DOMSubtreeModified",{ evnt: Event ->
                // .. enable or disable the TZ_OFFSET_HOUR field accordingly
                (document.getElementById(TZ_OFFSET_HOUR) as HTMLInputElement).disabled = (span.innerText !== "(x)")
            })
        }

        (document.getElementById(SHOW_EOT) as HTMLSpanElement?)?.let { span ->
            // When the SHOW_EOT check button state is changed ..
            span.addEventListener("DOMSubtreeModified",{ evnt: Event ->
                val disable =  (span.textContent == "{.}")

                listOf(EOT_FULL_MINUTES, EOT_HALF_MINUTES, EOT_NONE).forEach { id ->
                    // .. enable/disable EOT_FULL_MINUTES, EOT_HALF_MINUTES, EOT_NONE radio buttons  ..
                    document.getElementById(id)?.let { radio ->
                        if (disable) {
                            radio.classList.add("disabled")
                        }
                        else {
                            radio.classList.remove("disabled")
                        }
                    }
                }

                // .. enable/disable EOT_N field
                (document.getElementById(EOT_N) as HTMLInputElement?)?.let {
                    it.disabled = disable
                }
            })
        }

        initRadios()
        initCheckboxes()

        window.addEventListener("unload",{
            name().value?.let {
                // On exit, set the current name as a cookie
                document.cookie = "name=$it"
            }
        })

        if (!document.cookie.contains("quick_start")) {
            // Show the quick-start hint if this is the first time the page is opened in the browser
            document.getElementById("quick_start")?.classList?.remove("display_none")
            document.cookie = "quick_start=false;"
        }

        loadFromCookies()
    }

    override fun getInputValue(inputId: String) = when (inputId) {
        NAME, LATITUDE, LONGITUDE,
        AZIMUTH, ALTITUDE, HOUR_SPAN,TZ_OFFSET_HOUR,
        MIN_ALTITUDE_ABOVE, YEAR, WIDTH, HEIGHT, EOT_N,
        ANNIVERSARY-> (document.getElementById(inputId) as HTMLInputElement?)?.value// ?: ""
        CLIP_HORIZON, SHOW_EOT -> getCheckboxValue(inputId)
        MINUTE_STEP, ELONG_STEP, HOUR_SYMBOLS, TZ_MERIDIAN, ZOODIAC_SYMBOLS, ALIGNMENT, EOT_FMT -> checkedId(inputId)
        else -> null
    }

    fun getCheckboxValue(boxName: String) = (document.getElementById(boxName) as HTMLSpanElement?)?.let {
        if (it.innerHTML == "{v}")
            "true"
        else
            "false"
    }

    companion object {

//        val HMS_24 = Regex("""(24(:00){0,2})|((2[0-3]|1\d|0?\d)(:[0-5]\d){0,2})""")
//        val HM_12 = Regex("""[+,\-]?((12(:00){0,2})|((1[0-1]|0?\d)(:[0-5]\d)?))""")

        fun watchInput(id: String, validate:()->Input<*>) {
            (document.getElementById(id) as HTMLInputElement?)?.let { input ->
                input.addEventListener("input",{ event ->
                    input.style.color = colorStr(if (validate() is Error<*>) RED else GREEN)
                })
            }
        }

        fun listenForClick(id: String,action:()->Unit) {
            document.getElementById(id)?.let { elm ->
                elm.addEventListener("click",{ _: Event ->
                    action()
                })
            } ?: logC(PINK, "Failed to find id=$id")
        }

        fun saveAsCookie() {
            // save input as  a cookie: 'name'= { jason formatted input values}
            document.getElementsByTagName("input").asList().also { nodeList ->
                val map = mutableMapOf<String,JsonElement>()
                nodeList.forEach { elm ->
                    if (elm is HTMLInputElement) {
                        if ((elm.type=="text") || (elm.type=="date") || (elm.type=="time"))
                            if (elm.value.isNotEmpty())
                                map[elm.id] = JsonPrimitive(elm.value.trim())
                    }
                }

                document.getElementsByClassName("radio").asList().forEach { radio ->
                    if (radio is HTMLSpanElement)
                        if (radio.textContent == "(x)") {
                            map[radio.id] = JsonPrimitive(true)
                        }
                }

                document.getElementsByClassName("checkbox").asList().forEach { checkbox ->
                    if (checkbox is HTMLSpanElement)
                        if (checkbox.textContent == "{v}") {
                            map[checkbox.id] = JsonPrimitive(true)
                        }
                }

                (document.getElementById("name") as HTMLInputElement?)?.let { inp ->
                    val name = inp.value.trim()

                    if (name.isNotEmpty()) {
                        document.cookie = "${name}=${JsonObject(map.toMap())}".also {
                            logC(PINK, "cookie saved: $it}")
                        }
                    }
                }
            }

        }

        fun loadFromCookies() {
            if (document.cookie.isNotEmpty()) {
                // Build a map from the cookies
                val map = mutableMapOf<String, String>().also { m ->
                    document.cookie.split(';').forEach { cookie ->
                        val (key, value) = cookie.split('=', limit = 2)
                        m[key.trim()] = value.trim()
                    }
                }

                document.getElementById("parameters")?.let { datalist ->
                    val sb = StringBuilder()
                    map.entries.forEach { (k,_) ->
                        if (k=="name")
                            // if the key is "name" ..
                            map["name"]?.let {
                                // .. load the values
                                loadCookie(it)
                            }
                        else {
                            sb.append("<option value=\"$k\">${k.uppercase()}</option>")
                        }
                    }

                    datalist.innerHTML = sb.toString()
                }
            }
        }


        fun loadCookie(name: String) {
            if (document.cookie.isNotEmpty()) {
                // Make a map from <INPUT> ids to corresponding Json-formatted values
                val map = mutableMapOf<String, String>().also { m ->
                    document.cookie.split(';').forEach { cookie ->
                        val (key, value) = cookie.split('=', limit = 2)
                        m[key.trim()] = value.trim()
                    }
                }

                if (map.contains(name)) {
                    // For the current name ..
                    val sb = StringBuilder()
                    sb.append("$name loaded:\n")
                    map[name]?.let { cookie ->
                        if (cookie.isNotEmpty()) {
                            val j = Json.parseToJsonElement(cookie)
                            if (j is JsonObject)
                                // .. set the <INPUT>s to the stored values
                                j.entries.forEach { (k, v) ->
                                    if (v is JsonPrimitive) {
                                        val w = v.toString()
                                        // Remove the pair of end quotes in the value, if present
                                        val x = if (w.startsWith('"') && w.endsWith('"'))
                                            w.substring(1, w.length - 1)
                                        else
                                            w
                                        document.getElementById(k)?.let { input ->
                                            if (input is HTMLInputElement) {
                                                // Update value in input
                                                input.value = x
                                                // Trigger input event to check if the input.value is formatted correctly.
                                                // The "name" field does not require any format
                                                if (k != "name")
                                                    input.dispatchEvent(Event("input", EventInit(bubbles = true)))
                                            }
                                            else if (input is HTMLSelectElement)
                                                input.value = x // There are no <SELECT>s
                                            else if (input is HTMLSpanElement) {
                                                // The parent <span> has the click-listener. Trigger the
                                                // event to set the formerly selected "radio-button"
                                                input.parentElement?.dispatchEvent(Event("click"))
                                            }
                                        }
                                        sb.append(" [$k]=$x")
                                    }
                                }
                        }
                    }

                    logC(PINK, sb.toString())
                }
            }
        }

//        fun setInput(id: String,value: String) = (document.getElementById(id) as HTMLInputElement?)?.let { input ->
//            input.value = value
//        }

        // Get the checked radio button among all buttons with the name attribute set to the value og the group string
        fun checkedId(group: String) = document.getElementsByName(group).let { nodeList ->
            var i = 0
            var id: String? = null
            while (i < nodeList.length) {
                nodeList.item(i++)?.let { node ->
                    if ((node as Element).textContent == "(x)")
                        id = node.id
                }
            }

            id
        }

//        fun isHMS24(h: String) = HMS_24.matches(h)

//        fun isHM12(h: String) = HM_12.matches(h)

        fun setAzAlt(az: String,alt: String) {
            getInputField(AZIMUTH)?.also { input ->
                input.value = az
                input.dispatchEvent(Event("input", EventInit(bubbles = true)))
            }
            getInputField(ALTITUDE)?.also { input ->
                input.value = alt
                input.dispatchEvent(Event("input", EventInit(bubbles = true)))
            }
        }

        fun setWH(w: String,h: String) {
            getInputField(WIDTH)?.also { input ->
                input.value = w
                input.dispatchEvent(Event("input", EventInit(bubbles = true)))
            }
            getInputField(HEIGHT)?.also { input ->
                input.value = h
                input.dispatchEvent(Event("input", EventInit(bubbles = true)))
            }
        }

        fun getInputField(id: String) = document.getElementById(id) as HTMLInputElement?

