import kotlinx.browser.window
import kotlinx.serialization.ExperimentalSerializationApi
import kotlinx.serialization.decodeFromString
import kotlinx.serialization.json.Json
import org.w3c.dom.ErrorEvent
import org.w3c.dom.Worker
import org.w3c.dom.url.URL

class WebClient(
    val onAbort:(success: Boolean) -> Unit,
    val lock:(locked: Boolean)->Unit,
    val processCommand:(command: String, postToWorker:(message: String) -> Unit, lock:(locked: Boolean)->Unit) ->Boolean,
    val processData:(clazz: String, json: String, postToWorker:(message: String) -> Unit, lock:(locked: Boolean)->Unit) -> Boolean
) {

    @OptIn(ExperimentalSerializationApi::class)
    private var worker: Worker? = null
        set(value) {
            // If the worker-field is to be set to null, the worker is terminated first
            if (value == null)
                field?.let {
                    it.terminate()
                    onAbort(true)
                } ?: onAbort(false)

            field = value
        }
        get() = field ?: run {
            val w = Worker(URL("./worker.js", window.location.href).href)

            fun postToW(msg: String) = w.postMessage(msg)

            w.onerror = { logC(MAGENTA, "Worker error: ${if (it is ErrorEvent) it.message else it.type}") }

            w.onmessage = { me ->
                val msg = if (me.data is String) me.data as String else me.data.toString()
                val idx = msg.indexOf('{')

                if (idx > -1) {
                    val clazz = msg.substring(0, idx) // Data class name identifying the operation to perform
                    val json = msg.substring(idx, msg.length) // Json formatted data to process.

                    if (clazz == PrintData::class.simpleName) {
                        val printData = Json.decodeFromString<PrintData>(json)
                        when (printData.type) {
                            PRINT -> print(printData.color, printData.msg)
                            PRINT_LN -> printLn(printData.color, printData.msg)
//                            PRINT_PRE -> printPre(printData.color,printData.msg,printData.name)
                            PRINT_WRN -> printLnWrn(printData.msg)
                            PRINT_ERR -> printLnErr(printData.msg)
                            PRINT_TMP -> printLnTmp(printData.color,printData.msg)
                            else -> printLn(printData.color, printData.msg)
                        }
                    }
                    else if (clazz == PreData::class.simpleName) {
                        printPre(Json.decodeFromString<PreData>(json))
                    }
                    else if (!processData(clazz,json,::postToW,lock))
                        logC(MAGENTA,"Data class not understood by client: $clazz")
                    else {
                    }
                } else {
                    if (msg == BUSY)
                        lock(true)
                    else if (msg == DONE)
                        lock(false)
                    else if (!processCommand(msg,::postToW,lock))
                        logC(MAGENTA, "Command not understood by client: $msg ")
                    else {
                    }
                }
            }

            worker = w
            w
        }

    fun postToWorker(msg: String) = worker?.postMessage(msg)

    fun terminateWorker() {
        worker = null
    }
}