    fun wrt(e: HTMLElement,scrollTop: Double? = null) {
        (document.getElementById("output") as HTMLDivElement?)?.let { output ->
            (document.getElementById("cursor") as HTMLElement?)?.let { cursor ->

                output.insertBefore(e, cursor)

                scrollTop?.also {
                    (document.getElementById("monitor") as HTMLElement?)?.scrollTop = it
                } ?: cursor.scrollIntoView(false)
            }
        }
    }

    fun wrtPre(preData: PreData) {
        // Write a HTML <pre> tag
        document.createElement("div").also { div ->
            // Create a <div> in output to hold the <pre>
            (div as HTMLElement).apply {
                style.position = "relative"
            }

            // Add two buttons to the div
            div.innerHTML =
                """<span class='button' id='full' title='Full view' style='position: absolute;top: 0px;right: 0px;'>[⤢]</span>
                |
                |<span class='button' id='dl' title='Download' style='position: absolute;top: 0px;left: 0px;'>[⇓]</span>""".trimMargin()

            fun url(): String {
                // Format the <pre> as a URL with a blob
                val bag = BlobPropertyBag("text/")
                val blob = Blob(preData.data.toCharArray().toTypedArray(), bag)

                return URL.createObjectURL(blob)
            }

            // Add an event listener to the download button
            (div.children["dl"] as HTMLElement?)?.let { btn ->
                val name = preData.name ?: "OpenSCAD"
                val namePart = if (preData.namePart != null) ".${preData.namePart}" else ""
                val ext =  if (preData.ext != null) ".${preData.ext}" else ".txt"

                // Add code to download the <pre> by a click on the [⇓] button
                (btn as Node).addEventListener("click", {
                    // https://stackoverflow.com/questions/23582101/generating-viewing-and-saving-svg-client-side-in-browser
                    (document.createElement("a") as HTMLAnchorElement).also { a ->
                        a.href = url()
                        a.target = "_blank"
                        a.download = "$name$namePart$ext"
                        a.click()
                    }
                })
            }

            // Add an event listener to the fullscreen button
            (div.children["full"] as HTMLElement?)?.let { btn ->
                // Add code to open the svg in a new tab by a click on the [⤢] button
                (btn as Node).addEventListener("click", {
                    // https://stackoverflow.com/questions/23582101/generating-viewing-and-saving-svg-client-side-in-browser
                    window.open(url(), "_blank")
                })
            }

            // Frame the <pre> in a styled <span>
            (document.createElement("span") as HTMLSpanElement).let { span ->
                span.setAttribute("style", colorFG(preData.color))
                (document.createElement("pre") as HTMLPreElement).let { pre ->
                    pre.innerHTML = preData.data
                    span.append(pre)
                    div.append(span)
                }
            }

            wrt(div, scrollTop = preData.scrollTop)
        }
    }

    fun wrtSVG(svg: String,name: String? = null,namePart: String? = null) {
        document.createElement("div").also { div ->
            // Create a div in output to hold the svg
            (div as HTMLElement).apply {
                style.position = "relative"
            }

            // Add the svg and two buttons to the div
            div.innerHTML =
                """<span class='button' id='full' title='Full view' style='position: absolute;top: 0px;right: 0px;'>[⤢]</span>
                |
                |<span class='button' id='dl' title='Download' style='position: absolute;top: 0px;left: 0px;'>[⇓]</span>""".trimMargin()


            DOMParser().parseFromString(svg, "image/svg+xml").getElementsByTagName("svg")[0]?.let { svgElm ->

                fun pixelLength(q: String?) = q?.let {
                    splitMeasure(q)?.let { (value,u) ->
                        if (u == "mm")
                        // Convert a millimeter measure to pixels
                            pixPerMm*value
                        else
                        // Leave pixel measure unchanged
                            value
                    }
                }

                pixelLength(svgElm.getAttribute("width"))?.let { w -> // Width of svg in pixels
                    pixelLength(svgElm.getAttribute("height"))?.let { h -> // Height of svg in pixels
                        // Add a style to the svg-element to scale the svg to 98% of the div width. Note
                        // height: 0, or else the svg height will scale but the height it occupies will not scale!?

                        document.getElementById("monitor")?.let { monitor -> // The root div
                            val moH = (monitor as HTMLDivElement).clientHeight
                            val maxH = min(0.9*moH,h)
                            val maxW = min((w/h)*maxH,w)
                            svgElm.setAttribute("style", "width: 100%;height: 0%;max-width: ${maxW}px; max-height: ${maxH}px;")
                            // Style the created div and append svg
                            div.style.maxWidth = "${maxW}px"
                            div.style.maxHeight = "${maxH}px"
                            div.append(svgElm)
                        }
                    }
                }
            }

            fun url(): String {
                // Format the svg as a URL with a blob
                val bag = BlobPropertyBag("image/svg+xml")
                val blob = Blob(svg.toCharArray().toTypedArray(), bag)

                return URL.createObjectURL(blob)
            }

            // Add an event listener to the download button
            (div.children["dl"] as HTMLElement?)?.let { btn ->
                // Add code to download the svg by a click on the [⇓] button
                (btn as Node).addEventListener("click", {
                    // https://stackoverflow.com/questions/23582101/generating-viewing-and-saving-svg-client-side-in-browser
                    (document.createElement("a") as HTMLAnchorElement).also { a ->
                        a.href = url()
                        a.target = "_blank"
                        a.download = "${name ?: "svg"}${if (namePart != null) ".$namePart" else ""}.svg"
                        a.click()
                    }
                })
            }

            // Add an event listener to the fullscreen button
            (div.children["full"] as HTMLElement?)?.let { btn ->
                // Add code to open the svg in a new tab by a click on the [⤢] button
                (btn as Node).addEventListener("click", {
                    // https://stackoverflow.com/questions/23582101/generating-viewing-and-saving-svg-client-side-in-browser
                    window.open(url(), "_blank")
                })
            }

            wrt(div)
        }
    }

    fun wrtBG(fgColor: Int, bgColor: Int, s: String,isTemorary:Boolean=false) {
        // Write to output while setting both text color and background color
        (document.createElement("span") as HTMLSpanElement).also { span ->
            span.innerHTML = s
            span.style.color = colorStr(fgColor)
            span.style.background = colorStr(bgColor)

            wrt(span)

            (document.createElement("br") as HTMLBRElement).also { br ->
                wrt(br)
                if (isTemorary) {
                    span.setAttribute("tmp","")
                    br.setAttribute("tmp","")
                }
            }
        }
    }

    fun wrtLnTmp(color: Int, s: String) {
        (document.createElement("span") as HTMLSpanElement).also { span ->
            span.style.color = colorFG(color)
            span.innerHTML = s
            span.setAttribute("tmp","")

            wrt(span)
            (document.createElement("br") as HTMLBRElement).also { br ->
                br.setAttribute("tmp","")
                wrt(br)
            }
        }
    }

    val xableButtons = arrayOf("pi","eot","dial","scad")

    fun clr(slow: Boolean = true,done:(scrollTop: Double)->Unit={}) {
        (document.getElementById("monitor") as HTMLElement?)?.let { monitor ->
            val top = monitor.scrollTop
            (document.getElementById("output") as HTMLDivElement?)?.let { out ->
                CoroutineScope(Dispatchers.Main).launch {
                    document.getElementById("bayeux")?.also {
                        it.scrollIntoView(false)
                    }

                    while (out.childElementCount > 1) {
                        if (slow) delay(1)
                        out.removeChild(out.childNodes[out.childElementCount - 1] as Element)
                    }

                    done(top)
                }
            }
        }
    }

    fun clrTmp(slow: Boolean = true,done:()->Unit={}) {
        (document.getElementById("output") as HTMLDivElement?)?.let { out ->

            CoroutineScope(Dispatchers.Main).launch {
                out.childNodes.asList().reversed().forEach { node ->
                    if (node is HTMLElement)
                        if (node.attributes["tmp"] != null) {
                            node.scrollIntoView(false)
                            delay(1)
                            out.removeChild(node)
                        }
                }

                done()
            }
        }
    }

    fun buttonsDisable(disabled: Boolean,ids: Array<String>) {
        for (id in ids)
            (document.getElementById(id) as HTMLSpanElement?)?.also { span ->
                if (disabled) span.classList.add("disabled") else span.classList.remove("disabled")
            }
    }

    fun cursorVisible(visible: Boolean) = (document.getElementById("cursor") as HTMLElement?)?.also { cursor ->
        if (visible)
            cursor.classList.remove("hidden")
        else
            cursor.classList.add("hidden")
    }

    fun wrtAnchor(color: Int, a: String, style: String? = null) {
        (document.createElement("span") as HTMLSpanElement).also { span ->
            val sb = StringBuilder(colorFG(color))
            if (style != null) sb.append(style)
            span.setAttribute("style", sb.toString())
            span.innerHTML = a

            wrt(span)
        }
    }

    fun wrtLnAnchor(color: Int, a: String, style: String? = null) {
        wrtAnchor(color,a,style)

        (document.createElement("br") as HTMLBRElement).also { br ->
            wrt(br)
        }
    }

    fun wrtHTML(html: String,scrollTop: Double?=null) {
        (document.createElement("span") as HTMLSpanElement).also { span ->
            span.innerHTML = html

            wrt(span,scrollTop=scrollTop)
        }
    }

    print.printImp = object : PrintImplementation {
        override fun prt(color: Int,s: String) = w(color,s)
        override fun prtln(color: Int,s: String) = wLn(color,s)
        override fun prtPre(preData: PreData) = wrtPre(preData)
        override fun prtSvg(svgData: SvgData)  = wrtSVG(svgData.svg, name = svgData.name, namePart = svgData.namePart)
        override fun prtLnWrn(wrn: String) = wrtBG(PINK, BLACK, wrn,isTemorary = true)
        override fun prtLnErr(err: String) = wrtBG(RED, BLACK, err)
        override fun prtLnTmp(color: Int, s: String) = wrtLnTmp(color,s)

        private fun w(color: Int, s: String) {
            (document.createElement("span") as HTMLSpanElement).let { span ->
                span.innerHTML = s.replace("\n","<br>")
                span.setAttribute("style", colorFG(color))
                wrt(span)
            }
        }

        private fun wLn(color: Int, s: String) {
            w(color,s)

            wrt(document.createElement("br") as HTMLBRElement)
        }


        private fun wPre(color: Int, s: String) {
            (document.createElement("span") as HTMLSpanElement).let { span ->
                span.setAttribute("style", colorFG(color))
                (document.createElement("pre") as HTMLPreElement).let { pre ->
                    pre.innerHTML = s
                    span.append(pre)
                    wrt(span)
                }
            }
        }
    }

    measureText.measureTextImpl = object : MeasureTextImplementation {
        override suspend fun measureText(data: MeasuredTextData): MeasuredTextData {
            return measureText(data.txt, "normal ${(1.5*data.fontSizeMm).roundToInt()}px  $FONT_NAME").let { tm ->
                MeasuredTextData(
                    data.txt, data.fontSizeMm, tm.width,
                    tm.actualBoundingBoxAscent + tm.actualBoundingBoxDescent,
                    Vector(tm.actualBoundingBoxLeft, tm.actualBoundingBoxDescent)
                )
            }
        }
    }

    fun lock(locked: Boolean) {
        buttonsDisable(locked, xableButtons)
        cursorVisible(locked)
    }

    fun unlock() = lock(false)

    @OptIn(ExperimentalSerializationApi::class)
    val webClient = WebClient(
        // Action to be performed when terminating the worker
        { success ->
            if (success) {
                logC(RED, "worker.terminate() executed")
                printLn(RED, "Aborted")
        }
        else
            logC(ORANGE, "No worker to terminate ")
        },
        // Lock/unlick the GUI
        ::lock,
        // process commands
        { command,postToWorker,lock ->
            if (command == "clrtmp") {
                clrTmp {
                    postToWorker("cleared")
                }
                true
            }
            else
                false

        },
        // process data
        { clazz, json,postToWorker,lock ->
            var wasPerformed = false

            when (clazz) {
                // The data classes requiring access to the document should be handled here
                SvgData::class.simpleName -> {
                    // Display SVGs produced by the worker
                    (Json.decodeFromString<SvgData>(json)).also { svgData ->
                        printSVG(svgData)

                        wasPerformed = true
                    }
                }
                MeasuredTextData::class.simpleName -> {
                    // Measure text-size for the worker and post back the result
                    (Json.decodeFromString<MeasuredTextData>(json)).also { data ->
                        CoroutineScope(Dispatchers.Main).launch {
                            val mt = measuredText(data.txt, data.fontSizeMm)
                            postToWorker("$clazz${Json.encodeToString(mt)}")
                        }

                        wasPerformed = true
                    }
                }
                else -> {
                }
            }

            wasPerformed
        }
    )

