     lateinit var input: InputToDial

    window.addEventListener("load",{
        input = object : InputToDial() {
            override fun <P> message(err: Error<P>) {
                err.msg.split("£", limit = 2).also { lst ->
                    if (lst.size == 1)
                        printLn(GREEN,lst[0])
                    else {
                        print(GREEN,lst[0])
                        print(RED,err.ins?:"")
                        printLn(GREEN,lst[1])
                    }
                }
            }
        }
//        InputToDial.initiate()
    })

