            unlock()
        }
    }) ?: logC(MAGENTA,"No [CLR]")

    document.getElementById("pi")?.also { button ->
        button.addEventListener("click", { _: Event ->
            if (!button.classList.contains("disabled")) {
                clr(slow = false) {
                    wrtAnchor(LT_BLUE, "<a href='https://www.wikiwand.com/en/Approximations_of_%CF%80#Efficient_methods' target='_blank'>Method</a")
                    printLn(DK_GREEN, " for π/2 by Newton")
                }
                lock(true)
                val piData = PiData(50L)
                val msg = "${PiData::class.simpleName}${Json.encodeToString(piData)}"
                logC(LT_BLUE,"Client sending : $msg")
                webClient.postToWorker(msg)
            }
        })
    } ?: logC(MAGENTA,"No [π]")

    document.getElementById("eot")?.also { button ->
        button.addEventListener("click", { _: Event ->
            clr(slow = false) {

                var ok = true

                input.year().let { y ->
                    if (y is Error) {
                        ok = false
                        input.message(y)
                    }

                        input.latitude().let { lat ->
                            if (lat is Error) {
                                ok = false
                                input.message(lat)
                            }

                            input.longitude().let { lng ->
                                if (lng is Error) {
                                    ok = false
                                    input.message(lng)
                                }

                                input.azimuth().let { az ->
                                    if (az is Error) {
                                        ok = false
                                        input.message(az)
                                    }

                                    input.altitude().let { alt ->
                                        if (alt is Error) {
                                            ok = false
                                            input.message(alt)
                                        }

                                        input.minAltitudeAboveDial().let { minAlt ->
                                            if (minAlt is Error) {
                                                ok = false
                                                input.message(minAlt)
                                            }

                                            if (ok) {
                                                val eotData = EoTData(
                                                    y.value,
                                                    lat.value * DEG, lng.value * DEG,
                                                    az.value * DEG, alt.value * DEG,
                                                    minAlt.value, 0.0, 800.0, pixPerMm
                                                )
                                                val msg = "${EoTData::class.simpleName}${Json.encodeToString(eotData)}"
                                                logC(MAGENTA, "Client sending : $msg")
                                                webClient.postToWorker(msg)
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
        })
    } ?: logC(MAGENTA,"No [EoT]")

    document.getElementById("dial")?.also { button ->
